import { Component, Host, Prop, h } from '@stencil/core';
import { ICON_SIZES } from '../icons/exports';
import { DIALOG_VARIANTS } from './exports';

@Component({
  tag: 'mdx-dialog',
  styleUrl: 'dialog.scss',
  shadow: true,
})
export class Dialog {
  @Prop({ reflect: true })
  variant: DIALOG_VARIANTS = DIALOG_VARIANTS.DEFAULT;

  @Prop()
  open = false;

  @Prop()
  heading = '';

  render() {
    return this.open ? (
      <Host>
        <div class="dialog">
          <div class="dialog__icon-container">
            <slot name="icon">
              <mdx-icon-alert-triangle size={ICON_SIZES.SM} />
            </slot>
          </div>
          <div class="dialog__content">
            <div class="dialog__heading">{this.heading}</div>
            <div class="dialog__text">
              <slot name="content"></slot>
            </div>
            <div class="dialog__button-bar">
              <mdx-button-group>
                <slot name="button"></slot>
              </mdx-button-group>
            </div>
          </div>
        </div>
        <div class="dialog__backdrop"></div>
      </Host>
    ) : null;
  }
}
