:host .dialog {
  position: fixed;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  gap: var(--mdx-comp-dialog-content-gap-icon-text-horizontal);
  padding: var(--mdx-comp-dialog-content-spacing-padding-default);
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  box-shadow: var(--mdx-comp-dialog-shadow-desktop);
  background-color: var(--mdx-comp-dialog-background-color-default);
  border: 2px solid var(--mdx-comp-dialog-border-color-default);
  border-radius: var(--mdx-comp-dialog-border-radius-desktop);
  color: var(--mdx-comp-dialog-color-default);
  animation: visible var(--mdx-comp-dialog-transition-fade-in-out-duration)
    var(--mdx-comp-dialog-transition-fade-in-out-timing-function) var(--mdx-comp-dialog-transition-fade-in-out-delay);
  z-index: 1001;

  @media screen and (max-width: 768px) {
    flex-direction: column;
    align-items: flex-start;
    gap: var(--mdx-comp-dialog-content-gap-icon-text-vertical);
    box-shadow: var(--mdx-comp-dialog-shadow-mobile);
    border-radius: var(--mdx-comp-dialog-border-radius-mobile) var(--mdx-comp-dialog-border-radius-mobile) 0 0;
    width: 100%;
    max-height: 100vh;
    overflow-y: auto;
    max-height: -webkit-fill-available;
    bottom: 0;
    top: initial;
    transform: translate(-50%, 0%);
    animation: slide-in var(--mdx-comp-dialog-transition-fade-in-out-duration)
      var(--mdx-comp-dialog-transition-fade-in-out-timing-function) var(--mdx-comp-dialog-transition-fade-in-out-delay);
  }

  &__content {
    @media screen and (min-width: 769px) {
      max-height: calc(100vh - 2 * var(--mdx-comp-dialog-content-spacing-padding-default));
      margin-right: calc(-1 * var(--mdx-comp-dialog-content-spacing-padding-default));
      padding-right: var(--mdx-comp-dialog-content-spacing-padding-default);
      overflow-y: auto;
    }
  }

  &__heading {
    font: var(--mdx-comp-dialog-font-title);
  }

  &__text {
    margin-top: var(--mdx-comp-dialog-content-gap-title-text);
    font: var(--mdx-comp-dialog-font-paragraph);
  }

  &__button-bar {
    margin-top: var(--mdx-comp-dialog-content-gap-content-button-group);
    display: flex;
    justify-content: flex-end;
    gap: var(--mdx-comp-button-group-horizontal-margin-gap-between-default);

    @media screen and (max-width: 768px) {
      flex-direction: column;
    }
  }

  &__icon-container {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: var(--mdx-comp-dialog-icon-circle-sizing-default);
    width: var(--mdx-comp-dialog-icon-circle-sizing-default);
    height: var(--mdx-comp-dialog-icon-circle-sizing-default);
    background-color: var(--mdx-comp-dialog-icon-background-default);
    color: var(--mdx-comp-dialog-icon-color-default);
  }

  &__backdrop {
    background-color: rgba(0, 0, 0, 0.3);
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1000;
  }

  ::slotted([slot='icon']) {
    display: flex;
  }
}

:host([variant='error']) .dialog {
  border: 2px solid var(--mdx-comp-dialog-border-color-error);

  &__icon-container {
    background-color: var(--mdx-comp-dialog-icon-background-error);
    color: var(--mdx-comp-dialog-icon-color-error);
  }
}
:host([variant='success']) .dialog {
  border: 2px solid var(--mdx-comp-dialog-border-color-success);

  &__icon-container {
    background-color: var(--mdx-comp-dialog-icon-background-success);
    color: var(--mdx-comp-dialog-icon-color-success);
  }
}
:host([variant='accent']) .dialog {
  border: 2px solid var(--mdx-comp-dialog-border-color-accent);

  &__icon-container {
    background-color: var(--mdx-comp-dialog-icon-background-accent);
    color: var(--mdx-comp-dialog-icon-color-accent);
  }
}

@keyframes visible {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slide-in {
  from {
    opacity: 0;
    transform: translate(-50%, 100%);
  }
  to {
    opacity: 1;
    transform: translate(-50%, 0%);
  }
}
